import React from 'react'
import axios from 'axios'
import styles from '../SourceNewsList/SourceNewsList.module.css'
import { getNews , getFavourite } from '../../actions/index'
import { connect } from 'react-redux' 

class AllSources extends React.Component{

    state = {
        data : null,
        propFlag : false
    }

    componentDidMount()
    {
        console.log(this.props.data)
        if(this.props.data && this.props.data.length !== 0)
        {
            this.setState({
                data : this.props.data,
                propFlag : true
            })
        }else{
        this.getAllNews()
        }   
    }

    getAllNews = async() => {
        const response = await axios.get('https://newsapi.org/v2/top-headlines?country=us&apiKey=cdc214b170424ed284c4a71285125906')
        this.setState({
             data: response.data,
             propFlag : false
        })
    }
    render()
    {
        if(this.state.data)
        {
            let data = null
            let header = null
            if(this.state.propFlag){
                data = this.state.data
                header = "Favourites"
            }
            else {
                data = this.state.data.articles
                header = "All Sources"
            }
            console.log(this.state.data)
            const list_of_news = data.map(article => {
                return(
                    <div style = {{ height : '150px'}}>
                    <div key = {article.title} className = {styles.articleContainer} onClick ={ () => {this.props.getNews((article.title))}}>
                        <img className = {styles.imageContainer} src={article.urlToImage} alt = " " />
                        <div style = {{ display: 'flex' , flexDirection: 'column' , marginLeft : '5%'}}>
                <div className = "content">
                    {article.title}
                    </div>
                <div className = "date" style ={{ marginTop : '1%'}}>
                {new Date(article.publishedAt).toDateString()}
                </div>
                <i className={`star outline icon ${styles.star_icon}`} onClick = { () => { this.props.getFavourite(article)}} ></i>
                    </div>
                    </div>
                     </div> 
                )
            }) 
            return(
                <div>
                <div>
            <h1>{header}</h1>
                    </div>
                    <div className = {styles.listContainer}>
                        {list_of_news}
                        </div>
                    </div>
            )
        }
        return(
            <div>
                
                </div>
        )
        
    }
}
export default connect(null , {getNews , getFavourite})(AllSources)