import React from 'react'
import { NewsDetails , selectedSource } from '../../actions/index'
import { connect } from 'react-redux'
import styles from './Source.module.css'

class Sources extends React.Component{

    componentDidMount()
    {
        this.props.NewsDetails()
    }
    render()
    {
        if(this.props.data)
        {
        const sourcelist = this.props.data.sources.map( source => {
            return(
                <h2 key ={source.name} onClick = {() => {this.props.selectedSource(source.id)}}>
                    {source.name}
                    </h2>
            )
        })
        return(
           <div>
               <div>
                   <h1> Sources </h1>
                   </div>
                   <div className = {styles.source_container}>
                   <h2 onClick = {() => {this.props.selectedSource("All Sources")}}>
                    All Sources
                    </h2>
                       {sourcelist}
                       </div>
                       <div className = {styles.favourite_section} onClick = {() => {this.props.selectedSource("favourites")}}>
                           <div style = {{ paddingTop : '3%'}}>Favourites({this.props.fav_news})</div>
                           </div>
               </div>
        )
    }
    return(
        <div className="ui segment" style = {{ height : '100%' , width : '100%'}}>
        <div className="ui active inverted dimmer">
          <div className="ui large text loader">Loading</div>
        </div>
        </div>
    )
}
}
const mapStateToProps = (state) => {
    return { data : state.data , fav_news : state.favourite_news.length}
}

export default connect( mapStateToProps , { NewsDetails , selectedSource} )(Sources)