import React from "react";
import Sources from "./components/Sources/Sources";
import SourceNewsList from "./components/SourceNewsList/SourceNewsList";
import AllSources from './components/AllSources/AllSources'
import News from "./components/News/News";
import styles from "./Style.module.css";
import { connect } from 'react-redux' 

class App extends React.Component {
  render() {
    return (
      <div>
        <div className={styles.component_container}>
          <Sources />
         {console.log("The value of selected source (value in app.js)" + this.props.selected_source)}
          {this.props.selected_source === null || this.props.selected_source === "All Sources"? <AllSources /> : this.props.selected_source === "favourites" ? <AllSources data = {this.props.data} /> : <SourceNewsList /> }
          <News />
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return { selected_source : state.selected_source , 
            data : state.favourite_news}
}

export default connect(mapStateToProps , null)(App);
