import { fetchNewsDetails} from  '../APIS/index'


export const NewsDetails = () => async dispatch => {
    const response = await fetchNewsDetails.get()
    dispatch({ type : 'NEWS_DETAILS' , payload : response.data})
}

export const selectedSource = (source) => {
    return{
        type : 'SELECTED_SOURCE',
        payload: source
    }
}

export const getNews = (news) => {
    return{
        type : 'SELECTED_NEWS',
        payload: news
    }
}

export const getFavourite = (news) => {
    return{
        type : 'FAVOURITE_NEWS',
        payload : news
    }
}



