import React from 'react'
import { connect } from 'react-redux'
import axios from 'axios'
import styles from './News.module.css'

class News extends React.Component{

    state = {
        data: null
    }

    componentDidMount()
    {
        this.getInitialNews()
    }

    componentDidUpdate(prevProps)
    {
        console.log("This is the selected news : " + this.props.news)
        if(prevProps.news !== this.props.news){
            this.getNews()
        }
    }

    getInitialNews = async() => {
        const response = await axios.get(`https://newsapi.org/v2/top-headlines?q=nasa&apiKey=cdc214b170424ed284c4a71285125906`)
        this.setState({
            data : response.data
        })
    }

    getNews = async() => {
        const response = await axios.get(`https://newsapi.org/v2/top-headlines?q=${this.props.news}&apiKey=cdc214b170424ed284c4a71285125906`)
        this.setState({
            data : response.data
        })
    }
    render()
    {
        console.log(this.state.data)
         if(this.state.data)
         {
         const article = this.state.data.articles[0]
         console.log(article)
         return(
             <div className = {styles.container}>
                 <h1>{article.title}</h1>
                 <div>
                     <center><img className= {styles.image_container} src={article.urlToImage} alt = " "/></center>
                     </div>
                 <div className = "content">
                     <h3 style={{ paddingTop : '4%'}}>Author Name : {article.author}</h3>
                     <div className = "meta">
                         <div>
                             <a style={{ fontSize : '15px' , marginTop : '10%'}} href={article.url} rel="noopener noreferrer" target = "_blank">Link to Complete Article</a>
                             </div>
                         <div className = "date" style={{ fontSize : '15px' , marginTop : '5%'}}>Published At : {new Date(article.publishedAt).toDateString()}</div>
                         </div>
                         <div style = {{ paddingTop : '3%' , fontSize : '20px' , width : '90%'}}>
                         <p>{article.description}</p>
                         </div>
                     </div>
                 </div>
         )}
         else{
            return(<div>
               
                </div>)
        }
    }

}
const mapStateToProps = (state) => {
    return { news : state.selected_news}
}

export default connect(mapStateToProps , null) (News)