import React  from 'react'
import { connect } from 'react-redux'
import axios from 'axios'
import styles from './SourceNewsList.module.css'
import { getNews , getFavourite } from '../../actions/index'

class SourceNewsList extends React.Component{

    state= {
        data : null
    }
    componentDidMount()
    {
        this.getNews()
    }
     componentDidUpdate(prevProps)
     {
        if( prevProps.selectedSource !== this.props.selectedSource)
        {
            this.getNews()
        }
     }
    getNews = async() => {
        const response = await axios.get(`https://newsapi.org/v2/top-headlines?sources=${this.props.selectedSource}&apiKey=cdc214b170424ed284c4a71285125906`)
        this.setState({
            data : response.data
        })
    }
    handleClick = () => {
        
    }
    render()
    {
        if(this.state.data)
        {
       // console.log(this.state.data)
        let sourceName = this.state.data.articles[0].source.name
        const list_of_news = this.state.data.articles.map(article => {
            return(
                <div style = {{ height : '150px'}}>
                <div key = {article.title} className = {styles.articleContainer} onClick ={ () => {this.props.getNews(article.title)}}>
            { article.urlToImage ? <img className = {styles.imageContainer} src={article.urlToImage} alt = " " /> : <div className = "active dimmer"><div class="ui loader"></div></div>}
                <div style = {{ display: 'flex' , flexDirection: 'column' , marginLeft : '5%'}}>
                <div className = "content">
                    {article.title}
                    </div>
                <div className = "date" style ={{ marginTop : '1%'}}>
                {new Date(article.publishedAt).toDateString()} 
                </div>
                <i className={`star outline icon ${styles.star_icon}`} id= "star_icon" onClick = { () => { this.props.getFavourite(article) }} ></i>
                    </div>
                    </div>
                    </div>
            )
        }) 
        return(
            <div>
            <div>
            <h1>{sourceName}</h1>
                </div>
                <div className = {styles.listContainer}>
                    {list_of_news}
                    </div>
                </div>
        )
        }
        return(
            <div>
                <h1>All Sources</h1>
                </div>
        )
    }
}

const mapStateToProps = (state) => {
    return { selectedSource : state.selected_source}
}

export default connect(mapStateToProps , { getNews , getFavourite })(SourceNewsList)