const INITIAL_STATE = {
    data : null,
    selected_source : null,
    selected_news : null,
    favourite_news : []
}

export const newsAPIData = ( state = INITIAL_STATE ,  action) => {
    switch(action.type)
    {
        case "NEWS_DETAILS" : return { ...state , data : action.payload}

        case "SELECTED_SOURCE" : return {...state , selected_source : action.payload }

        case "SELECTED_NEWS" : return {...state , selected_news : action.payload }

        case "FAVOURITE_NEWS" : return {...state , favourite_news : [...state.favourite_news , action.payload] }

        default: return state
    }
}   